import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'date',
                explain: '[(date)]=\'date1\'',
                type: 'any',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'original',
                explain: '[(original)]=\'date1\'',
                type: 'any',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'dateType',
                explain: '',
                type: 'String',
                optionaValue: 'displayDate,originalDate',
                defaultValue: 'displayDate'
            },
            {
                name: 'format',
                explain: `format[format]=""`,
                type: 'String',
                optionaValue: 'dddDDDmmmMMMyyyyyy',
                defaultValue: 'yyyymmdd'
            },
            {
                name: 'todayHighlight',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'true'
            },
            {
                name: 'autoclose',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'true'
            },
            {
                name: 'calendarWeeks',
                explain: ' ',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'clearBtn',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'todayBtn',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'startView',
                explain: '',
                type: 'String',
                optionaValue: 'days,months,years',
                defaultValue: 'days'
            },
            {
                name: 'minViewMode',
                explain: ' ',
                type: 'String',
                optionaValue: 'days,months,years',
                defaultValue: 'days'
            },
            {
                name: 'maxViewMode',
                explain: '',
                type: 'String',
                optionaValue: 'days,months,years',
                defaultValue: 'years'
            },
            {
                name: 'multidate',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'multidateSeparator',
                explain: ' ',
                type: 'String',
                optionaValue: '',
                defaultValue: ','
            },
            {
                name: 'orientation',
                explain: '',
                type: 'String',
                optionaValue: 'left,right,bottom,auto,top left,top right,auto top,auto bottom',
                defaultValue: 'bottom'
            },
            {
                name: 'startDate',
                explain: '',
                type: 'string',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'endDate',
                explain: '',
                type: 'string',
                optionaValue: '',
                defaultValue: '-'
            }
        ];
    };
};



